      <!-- <el-table-column prop="Receivableslist" label="" sortable></el-table-column> -->
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="contractdate" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="Receivablesend" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="weikaifapiao" label="" sortable></el-table-column>
      <el-table-column prop="projectClass" label="" sortable></el-table-column>
      <el-table-column prop="" label="" >
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small">{{scope.row.Receivableslistbo}}</el-button>
          <el-button @click="handle(scope.row)" type="text" size="small"></el-button>
