 <template>
  <el-main style="text-align:left; line-height: 1.8em;">
    <h3>
      <span @click="goToHome" style="cursor: pointer;color:#409EFF">{{this.projectName}}- > </span>
      <el-button type="primary" style="float: right;" @click="dialogFormVisible = true;imageUrlbo = false;form.ReceivablesData = ''"></el-button>
      <el-button style="float: right;margin-right:20px" onclick="exportExcel('#RevenueContract')"></el-button>
    </h3>
    <el-dialog title="" :visible.sync="dialogFormVisible">
      <el-form ref="form" :model="form" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="ReceivablesName">
          <el-input v-model="form.ReceivablesName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesData">
          <el-col :span="11" style="margin-right:10px">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="form.ReceivablesData" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesMode">
            <el-select v-model="form.ReceivablesMode" placeholder="" style="width:46%" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="number">
          <el-input type="number" v-model="form.number" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="contract">
            <el-select v-model="form.contract" placeholder="" style="width:46%" >
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            </el-select>
            <el-select v-model="form.Receivableslist" placeholder="" style="width:46%" >
              <el-option label="1" value="1"></el-option>
              <el-option label="2" value="2"></el-option>
              <el-option label="3" value="3"></el-option>
              <el-option label="4" value="4"></el-option>
              <el-option label="5" value="5"></el-option>
              <el-option label="6" value="6"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Remarks">
          <el-input type="textarea" v-model="form.Remarks" ></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-upload
            class="avatar-uploader"
            :action="upimgaction"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
              <img v-if="false" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <el-button style="vertical-align: top; margin-left:20px" @click="getimgurl()" v-if="imageUrlbo"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('form')"></el-button>
          <el-button @click="resetForm('form')"></el-button>
          <el-button @click="dialogFormVisible = false"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>

    <el-dialog title="" :visible.sync="dialogAddVisible" >
      <el-form ref="formModify" :model="formModify" :rules="rules"  label-width="80px" class="demo-ruleForm">
        <el-form-item label="" prop="ReceivablesName">
          <el-input v-model="formModify.ReceivablesName"></el-input>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesData">
          <el-col :span="11" style="margin-right:10px">
            <el-date-picker value-format="yyyy-MM-dd" type="date" placeholder="" v-model="formModify.ReceivablesData" style="width: 100%;"></el-date-picker>
          </el-col>
        </el-form-item>
        <el-form-item label="" prop="ReceivablesMode">
            <el-select v-model="formModify.ReceivablesMode" placeholder="" style="width:46%" >
              <el-option label="" value=""></el-option>
              <el-option label="" value=""></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="number">
          <el-input type="number" v-model="formModify.number" style="width:46%" ></el-input>
        </el-form-item>
        <el-form-item label="" prop="contract">
            <el-select v-model="formModify.contract" placeholder="" style="width:46%" >
            <el-option label="" value=""></el-option>
            <el-option label="" value=""></el-option>
            </el-select>
            <el-select v-model="formModify.Receivableslist" placeholder="" style="width:46%" >
              <el-option label="1" value="1"></el-option>
              <el-option label="2" value="2"></el-option>
              <el-option label="3" value="3"></el-option>
              <el-option label="4" value="4"></el-option>
              <el-option label="5" value="5"></el-option>
              <el-option label="6" value="6"></el-option>
            </el-select>
        </el-form-item>
        <el-form-item label="" prop="Remarks">
          <el-input type="textarea" v-model="formModify.Remarks" ></el-input>
        </el-form-item>
        <el-form-item label="" >
          <el-upload
            class="avatar-uploader"
            :action="upimgaction"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
              <img v-if="false" class="avatar">
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <el-button style="vertical-align: top; margin-left:20px" @click="getimgurl()" v-if="imageUrlbo"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitFormModify('formModify')"></el-button>
          <el-button @click="dialogAddVisible = false"></el-button>
          <el-button style="float:right" @click="outerVisible = true"></el-button>
        </el-form-item>
      </el-form>
    </el-dialog>
    <el-dialog
        width="30%"
        title=""
        :visible.sync="outerVisible"
        append-to-body>
        <el-button @click="outerVisible = false"></el-button>
        <el-button @click="deletepost"></el-button>
    </el-dialog>
    <el-table id="RevenueContract" :data="tableData" border :summary-method="jsondata.getSummaries" show-summary style="width: 100%">
      <el-table-column type="index"></el-table-column>
      <el-table-column @cell-click="handle" prop="ReceivablesName" label=""  width="300" sortable></el-table-column>
      <el-table-column prop="Receivableslist" label="" sortable></el-table-column>
      <el-table-column prop="number" label="" sortable></el-table-column>
      <el-table-column prop="ReceivablesData" label="" sortable></el-table-column>
      <el-table-column prop="Receivables" label="" sortable></el-table-column>
      <el-table-column prop="Receivablesend" label="" sortable></el-table-column>
      <el-table-column prop="invoice" label="" sortable></el-table-column>
      <el-table-column prop="weikaifapiao" label="" sortable></el-table-column>
      <el-table-column prop="" label="" >
        <template slot-scope="scope">
          <el-button @click="handleClick(scope.row)" type="text" size="small"></el-button>
          <el-button @click="handle(scope.row)" type="text" size="small"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-main>
</template>
<script>
// import axios from 'axios'
export default {
  data () {
    return {
      imageUrlbo: false,
      imageUrl: '',
      upimgaction: 'http://localhost:3000/uploads',
      outerVisible: false,
      weikaifapiao: 0, // 
      projectName: '',
      public: [],
      Customerlist: [],
      tableData: [],
      dialogAddVisible: false,
      dialogFormVisible: false,
      formModify: {
        id: '',
        ReceivablesName: '',
        ReceivablesData: '',
        ReceivablesMode: '',
        number: '',
        contract: '',
        Remarks: '',
        projectId: this.$route.params.id,
        Receivableslist: ''
      }, // 
      TransactionList: {},
      formTransactionCon: '',
      formTransactionList: [], // 
      formTransaction: {
        MoneyClass: '',
        CollectMoney: '',
        AmountMoney: '',
        invoice: '',
        projectId: this.$route.params.id
      }, // 
      form: {
        ReceivablesName: '',
        ReceivablesData: '',
        ReceivablesMode: '',
        number: '',
        contract: '',
        Remarks: '',
        projectId: this.$route.params.id,
        Receivableslist: ''
      },
      rules: {
        ReceivablesName: [
          { required: true, message: '', trigger: 'blur' }
        ]
        // ,
        // ReceivablesData: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // ReceivablesMode: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // contract: [
        //   { required: true, message: '', trigger: 'blur' }
        // ],
        // number: [
        //   { required: true, message: '' }
        // ],
        // formTransactionCon: [
        //   { required: true, message: '' }
        // ]
      }
    }
  },
  props: { // 
    map: {
      // type:String
    }
  },
  watch: {
  },
  mounted () {
    // ~~~~~~~~~~~~~~~~~~~~~~~~~~~~~~
    if (window.location.host.match('localhost')) {
      this.upimgaction = 'http://localhost:3000/uploads'
    } else {
      this.upimgaction = '/uploads'
    }
    this.getdata()
    this.getFormDataPojname()
  },
  methods: {
    getimgurl () {
      window.open('http://' + window.location.host + '/uploads/' + this.formModify.ReceivablesData, 'target', '')
    },
    handleAvatarSuccess (res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw)
      this.form.ReceivablesData = res.filename
      this.formModify.ReceivablesData = res.filename
      this.imageUrlbo = true
      this.$message.success(',!')
    },
    beforeAvatarUpload (file) {
      const isJPG = file.type === 'image/jpeg' || file.type === 'image/png' || file.type === 'application/pdf'
      // const isLt2M = file.size / 1024 / 1024 < 2
      if (!isJPG) {
        this.$message.error('!')
      }
      // const isJPG = file.type === 'image/jpeg'
      // const isLt2M = file.size / 1024 / 1024 < 2
      // if (!isJPG) {
      //   this.$message.error(' JPG !')
      // }
      // if (!isLt2M) {
      //   this.$message.error(' 2MB!')
      // }
      return isJPG
    },
    submitFormModify (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.updatpostData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    updatpostData () { // 
      this.jsondata.updatpostData('revenuecontract', this.formModify).then(response => {
        // this.jsondata.postDatabf(response.data, 'updatpostData')
        this.dialogAddVisible = false
        this.getdata()
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    submitForm (formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          this.postData()
        } else {
          console.log('error submit!!')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    postData () { // 
      this.jsondata.postData('revenuecontract', this.form).then(response => {
        console.log(response.data, this.form)
        if (response.data === 'OK') {
          console.log(response.data, this.form)
          this.dialogFormVisible = false
          this.getdata()
          this.resetForm('form')
        }
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    getdata () { // 
      this.weikaifapiao = 0 // 
      this.jsondata.getData('receivables').then(responselist => {
        this.formTransactionList = responselist.data
        // console.log(responselist.data)
        this.jsondata.getDataClass('revenuecontract', this.$route.params.id, 'projectId').then(response => {
          this.tableData = response.data
          for (let i = 0; i < this.tableData.length; i++) {
            // this.tableData[i].Receivablesend = this.jsondata.currency(this.tableData[i].number - this.tableData[i].Receivables, '', 2)
            this.tableData[i].Receivables = 0
            this.tableData[i].invoice = 0
            this.tableData[i].Receivablesend = 0
            this.tableData[i].weikaifapiao = 0
            for (let is = 0; is < this.formTransactionList.length; is++) {
              if (this.tableData[i].id == this.formTransactionList[is].projectId) { //eslint-disable-line
                this.tableData[i].Receivables += Number(this.formTransactionList[is].Receivables)
                this.tableData[i].invoice += Number(this.formTransactionList[is].invoice)
              }
            }
            // if (this.tableData[i].Receivables > this.tableData[i].invoice) {
            //   this.tableData[i].weikaifapiao = 0
            // } else {
            //   this.tableData[i].weikaifapiao = Number(this.tableData[i].invoice) - Number(this.tableData[i].Receivables)
            // }
            this.tableData[i].weikaifapiao = this.jsondata.currency(Number(this.tableData[i].invoice) - Number(this.tableData[i].Receivables), '', 2)
            this.tableData[i].Receivablesend = this.jsondata.currency(this.tableData[i].number - this.tableData[i].Receivables, '', 2)
            this.tableData[i].Receivables = this.jsondata.currency(this.tableData[i].Receivables, '', 2)
            this.tableData[i].invoice = this.jsondata.currency(this.tableData[i].invoice, '', 2)
            this.tableData[i].number = this.jsondata.currency(this.tableData[i].number, '', 2)
            // console.log(this.tableData[i])
          }
        })
          .catch(error => {
            console.log(error)
          })
      })
        .catch(error => {
          console.log(error)
        })

      return false
    },
    deletepost () { // 
      this.jsondata.deletepost('revenuecontract', this.formModify.id).then(response => {
        console.log(response.data)
        if (response.data === 'OK') {
          this.dialogAddVisible = false
          this.outerVisible = false
          this.getdata()
        }
      })
        .catch(error => {
          console.log(error)
        })
    },

    handleClick (row) {
      this.$router.push('/Receivables/' + row.id)
    },
    handle (row) {
      this.dialogAddVisible = true
      this.jsondata.getDataId('revenuecontract', row.id).then(response => {
        this.formModify = response.data[0]
        if (this.formModify.ReceivablesData === '') {
          this.imageUrlbo = false
        } else {
          this.imageUrlbo = true
        }
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    postFormData (formName, jsonData) {
      this.jsondata.postData(formName, jsonData).then(response => {
        console.log(response.data)
      })
        .catch(error => {
          console.log(error)
        })
      return false
    },
    getFormData (formName) {
      this.jsondata.getData('getposts').then(response => {
        this.public = response
        return this.public
      })
        .catch(error => {
          console.log(error)
        })
    },
    getFormDataPojname () {
      this.jsondata.getDataId('projectlist', this.$route.params.id).then(response => {
        this.projectName = response.data[0].projectName
      })
        .catch(error => {
          console.log(error)
        })
    },
    goToHome () {
      this.$router.push('/ProjectDetails/' + this.$route.params.id)
    }
  }
}
</script>



